    ul {
        list-style-type: none;
        list-style-position: inside;
        height: 100%;
        width: 100%;
        padding: 10px 10px;
        text-align: left;
        font-family: 'Inter';
        color: #F5F5F5;

        li {
            padding: 10px 0px;
        }
    }

    .checkbox {
        display: grid;
        flex-wrap: nowrap;
        height: 15px;
        width: 15px;
        margin: 0px 0px;
    }
